import React from 'react';
import { useRecoilValue, useRecoilState } from 'recoil';
import { userName, userGender } from '../../recoil/atom';
import styled from "styled-components";

const GenderSelect = () => {
  const name = useRecoilValue(userName);
  const [gender, setGender] = useRecoilState(userGender);

  const handleChange = (event) => {
    setGender(event.target.value);
  };

  const isDisabled = !name; //   true ()

  return (
    <>
      <h3> </h3>
      <Container>
        <RadioGroup $gender={gender} value="" disabled={isDisabled}>
          <Label htmlFor="male" checked={gender === ''}>
            (Male)
            <Checkbox
              type="radio"
              id="male"
              name="gender"
              value=""
              checked={gender === ''}
              onChange={handleChange}
              disabled={isDisabled}  //     
            />
          </Label>
        </RadioGroup>
        <RadioGroup $gender={gender} value="" disabled={isDisabled}>
          <Label htmlFor="female" checked={gender === ''}>
            (Female)
            <Checkbox
              type="radio"
              id="female"
              name="gender"
              value=""
              checked={gender === ''}
              onChange={handleChange}
              disabled={isDisabled}  //     
            />
          </Label>
        </RadioGroup>
      </Container>
    </>
  );
}

export default GenderSelect;

const Container = styled.div`
  display: flex;
  justify-content:space-between;
  align-items: center;
  border-radius: 8px;
`;

const RadioGroup = styled.div`
  position: relative;
  display: flex;
  width: 49.7%;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  overflow: hidden;
  background-color: ${props => props.disabled ? '#ccc' : (props.$gender === props.value ? (props.value === '' ? '#3182f6' : 'tomato') : '#ccc')};
`;

const Label = styled.label`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 15px;
  color: ${props => props.checked ? '#fff' : '#333'};
  cursor: ${props => props.disabled ? 'not-allowed' : 'pointer'};
  transition: all 0.25s linear;
`;

const Checkbox = styled.input`
  opacity: 0;
  position: absolute;
  cursor: pointer;
  width: 1em;
  height: 1em;
  margin-left: 10px;
  &:focus + ${Label} {
    box-shadow: 0 0 0 3px rgba(21, 156, 228, 0.4);
  }
`;
