import React, { useEffect, useState } from 'react';
import { useRecoilState, useRecoilValue } from 'recoil';
import { timeState } from '../../recoil/atom';
import { isDateEnteredSelector } from '../../recoil/selector';
import styled from "styled-components";

const TimeSelector = () => {
  const [isTimeUnknown, setIsTimeUnknown] = useState(false);
  const [selectedTime, setSelectedTime] = useRecoilState(timeState);
  const isDateEntered = useRecoilValue(isDateEnteredSelector);

  useEffect(() => {
    if (isTimeUnknown) {
      setSelectedTime('default');  // ''   
    }
  }, [isTimeUnknown, setSelectedTime]);  //   isTimeUnknown setSelectedTime 

  //    
  const times = [
    { value: "", label: "23:30 ~ 01:30 ()" },
    { value: "", label: "01:30 ~ 03:30 ()" },
    { value: "", label: "03:30 ~ 05:30 ()" },
    { value: "", label: "05:30 ~ 07:30 ()" },
    { value: "", label: "07:30 ~ 09:30 ()" },
    { value: "", label: "09:30 ~ 11:30 ()" },
    { value: "", label: "11:30 ~ 13:30 ()" },
    { value: "", label: "13:30 ~ 15:30 ()" },
    { value: "", label: "15:30 ~ 17:30 ()" },
    { value: "", label: "17:30 ~ 19:30 ()" },
    { value: "", label: "19:30 ~ 21:30 ()" },
    { value: "", label: "21:30 ~ 23:30 ()" }
  ];
  const handleChange = (event) => {
    if (!isDateEntered) {
      alert('  .');
      document.getElementById("date").focus();
      return;
    }
    setSelectedTime(event.target.value);
  };

  const isDisabled = !isDateEntered || isTimeUnknown;

  return (
    <>
      <Label htmlFor="selectedTime"></Label>
      <Flexbox>
      <Select
        id="selectedTime"
        value={selectedTime}
        onChange={handleChange}
        disabled={isDisabled}
      >
        <option value="default"> </option>
        {times.map(time => (
          <option key={time.value} value={time.value}>{time.label}</option>
        ))}
      </Select>
      <Switch>
        <input
          id="noTime"
          type="checkbox"
          checked={isTimeUnknown}
          onChange={e => setIsTimeUnknown(e.target.checked)}
          disabled={!isDateEntered}
        />
        <label htmlFor="noTime"></label>
      </Switch>
    </Flexbox>
    </>
  );
}

export default TimeSelector;

const Label = styled.label`
  display:inline-block;
  margin-bottom:12px;
  letter-spacing:0.05rem;
`
const Flexbox = styled.div`
  display:flex;
  align-items:center;
  [type="checkbox"] {
    appearance: none;
    position: relative;
    border: max(2px, 0.1em) solid gray;
    border-radius: 1.25em;
    width: 2.25em;
    height: 1.25em;
    cursor:pointer;
  }
  [type="checkbox"]::before {
    content: "";
    position: absolute;
    left: 0;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    transform: scale(0.8);
    background-color: gray;
    transition: left 0.25s linear;
  }
  [type="checkbox"]:checked {
    background-color: tomato;
    border-color: tomato;
  }
  [type="checkbox"]:checked::before {
    background-color: white;
    left: 1em;
  }
`
const Select = styled.select`
  width: 80%;
  height: 40px;
  padding:0 9px;
  border-radius: 8px;
  margin-right: 24px;
  border: 1px solid #333;
  background-color: ${props => props.disabled ? '#f4f4f4' : 'white'};
`;

const Switch = styled.div`
  display: flex;
  align-items: center;
  label {
    margin-left: 4px;
  }
`;
