//     
export const findColor = (code, colorPillar) => {
  const item = colorPillar.find(ele => ele.code === code);
  return item ? item.color : 'transparent';
};

//   
export const findFiveElements = (arr, Pillar) => {
  const five = {
    '': 0, '': 0, '': 0, '': 0, '': 0
  };
  arr.forEach(ele => {
    const element = Pillar.find(el => el.code === ele);
    if (element) {
      five[element.symbol]++;
    }
  });
  return five;
};


