import React from 'react';
import { useRecoilState, useSetRecoilState } from 'recoil';
import { userName, userGender, birthdayActiveState } from '../../recoil/atom';  // Import   atom  .
import styled from "styled-components";

const UserName = () => {
  const [user, setUserName] = useRecoilState(userName);
  const setGender = useSetRecoilState(userGender);
  const setBirthday = useSetRecoilState(birthdayActiveState);
  const nameOnChange = (event) => {
    let { value } = event.target;
    value = value.replace(/[^a-zA-Z-|-|-\s]/g, '');
    if (value.length <= 14) {
      setUserName(value);
      if(value.length > 0){
        setGender('')
        setBirthday(false)
      }
      if(value.length === 0){
        setGender('')
        setBirthday(true)
      }
    }
  };
  
  return (
    <>
      <Label htmlFor="userNameInput"></Label>
      <NameInput
        id="userNameInput"
        type="text"
        value={user}
        onChange={nameOnChange}
        placeholder=" 14 ."
      />
    </>
  );
};

export default UserName;

const NameInput = styled.input`
  width: 100%;
  height: 40px;
  border-radius: 8px;
  padding: 0 9px;
  border: 1px solid #333;
  letter-spacing: 0.04rem;
`
const Label = styled.label`
  display:block;
  margin-bottom:12px;
  letter-spacing:0.05rem;
`
